<template>
    <div class="flod-box">
        <div class="btns">
            <slot name="btns-start"></slot>
            <slot name="btns-end"></slot>
        </div>
        <Row class="publc-bottom10" v-for="(item,index) in list" :key="index">
            <Col span="24">
                <div class="item-box">
                    <div class="header">
                        <span>00{{index + 1}}单次委托</span>
                        <div>
                            <span class="btn">
                                <span @click="goodsOk(item,index)">货好确认</span>
                                <span @click="seePlan(item,index)">查看全程物流计划</span>
                                <span @click="addPlan(item,index)">生成全程物流计划</span>
                                <span v-if="!item.isContentShow" @click="onOpen(item,index)">展开</span>
                                <span v-if="item.isContentShow" @click="onClose(item,index)">收起</span>
                                <span style="cursor: text;">{{item.entrustExecutoryStatus}}</span>
                            </span>
                            <span class="icon">
                                <Icon v-if="item.isContentShow" type="ios-arrow-down" />
                                <Icon v-else type="ios-arrow-up" />
                            </span>
                        </div>
                    </div>
                    <!--这里的可以使用动画-->
                    <div class="main-box" v-show="item.isContentShow">
                        <!-- 桥套的子组件 -->
                        <cargo-info :arrChild="item"></cargo-info>
                    </div>
                </div>
            </Col>
        </Row>
    </div>
</template>

<script>
import CargoInfo from './CargoInfo5-1'
export default {
    components: {
        CargoInfo
    },
    props: {
        arr: {
            type: Array,
            default: []
        }
    },
    data() {
        return {
            list: [{ isContentShow: false }, { isContentShow: false }] // 得到的数据
        }
    },
    watch: {},
    computed: {
        cptList() {
            return this.arr
        }
    },
    created() {
        this.initList()
    },
    mounted() {},
    methods: {
        initList() {
            let arr = this.cptList.map(item => {
                return {
                    ...item,
                    isContentShow: false
                }
            })
            this.list = arr
        },
        //查看物流全程计划
        seePlan(item,index){
            alert("查看物流全程计划")
        },
        //生成物流全程计划
        addPlan(item,index){
            alert("生成物流全程计划")
        },
        //货好确认
        goodsOk(item,index){// 已货好
            alert("货好确认")
        },
        //查看物流全程计划
        seePlan(item,index){
            alert("查看物流全程计划")
        },
        onOpen(item, index) {
            //打开
            // let off = this.list.some(op => op.isContentShow == true)
            // if (off) {
            //     this.$Message.error('请先保存编辑项')
            //     return
            // }
            this.list[index].isContentShow = true
        },
        onClose(item, index) {
            // 收起
            this.list[index].isContentShow = false
        }
    }
}
</script>

<style lang="scss" scoped>
.flod-box {
    .btns {
        padding: 8px 0;
        /deep/.ivu-btn {
            margin-right: 12px;
        }
    }
    .item-box {
        width: 100%;
    }
    .header {
        width: 100%;
        height: 40px;
        padding: 0 20px;
        background: #fff6e5;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .btn > span {
            border: 1px solid #ccc;
            background: #fff;
            height: 27px;
            line-height: 27px;
            padding: 4px 8px;
            border-radius: 4px;
            margin: 0 10px;
            cursor: pointer;
        }
    }
    .main-box {
        width: 100%;
        padding: 5px;
        background: #f9f9f9;
    }
}
</style>